import './App.scss';
import IndexView from './views/Index';
import {Switch, Route, Redirect} from 'react-router-dom';
import FAQView from './views/FAQ';
import PackagesView from './views/Packages';

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact path="/">
          <IndexView/>
        </Route>
        <Route exact path="/faq">
          <FAQView/>
        </Route>
        <Route exact path="/packages">
          <PackagesView/>
        </Route>
        <Redirect to="/"/>
      </Switch>
    </div>
  );
}

export default App;
