import React from 'react';
import {Link} from 'react-router-dom';
import PlutoStyle from '../ui/PlutoStyle';

export default function FAQView() {
    return (
        <PlutoStyle>
            <h1>FAQ</h1>
            
            <section>
                <h2>How does this service work?</h2>
                <p>
                    At it's core this service simply converts your notebook to an HTML file, then runs it online with <a href="https://github.com/JuliaPluto/PlutoSliderServer.jl">PlutoSliderServer.jl</a>. Doing so at large scale and allowing anyone to upload their notebook is where this service distinguishes itself.
                </p>
            </section>
            <section>
                <h2>How many notebooks can I upload?</h2>
                <p>
                    For the time being, as many as you want! As demand fluctuates in the future this may or may not remain true, given the strain that processing notebook submissions can have.
                </p>
            </section>
            <section>
                <h2>Can I delete or update existing notebooks?</h2>
                <p>
                    Not currently, since we have no way to verify you as the original author. Feel free to just submit an updated notebook as a new notebook though.
                </p>
                <p>
                    Depending on demand and usage we may implement a user authentication system, which would solve these problems at the cost of an additional step for the user.
                </p>
            </section>
            <section>
                <h2>Why does my notebook have errors?</h2>
                <p>
                    Assuming everything works on your local Pluto editor, there are a few things that could be happening. Firstly, if your notebook requires external packages that aren't listed <Link to="/packages">here</Link>, you'll need to make a few modifications to your notebook to tell us how to install them.
                </p>
                <p>
                    References to local files will also not work, meaning you'll have to either include the contents of those files in the notebook somewhere, or download them inside the notebook from a public location such as <a target="_blank" href="https://pages.github.com/">GitHub Pages</a>.
                </p>
            </section>
            <section>
                <h2 id="state-files">What are Pluto state files?</h2>
                <p>
                    Fundamentally a Pluto notebook, unlike a Jupyter notebook, stores no cell outputs. Although this has numerous benefits for file format, it makes publishing services like this one difficult to manage. In order to obtain cell outputs we must run each notebook in a secure and isolated environment, which causes problems for some notebooks. If for whatever reason our service can't run your notebook, you can still publish it by providing us with a file containing cell outputs. This is exactly what a Pluto state file does.
                </p>
                <p>
                    There are a few caveats to this approach though, the main one being we cannot make notebooks interactive if they cannot be run on our service. Viewing features will still work fine, but sliders, text inputs, or any other cell using <code>@bind</code> will not work.
                </p>
            </section>
        </PlutoStyle>
    );
}
