import React, {useEffect, useRef, useState} from 'react';
import { Link } from 'react-router-dom';
import PlutoStyle from '../ui/PlutoStyle';
import Spinner from '../ui/Spinner';
import './Index.scss';


const Notebook = ({file, author, path, interactive, onRemove}) => (
    <div className="Notebook">
        <a href={`/nb/${path}`}>
            <h4>{file}&nbsp;&nbsp;{interactive ? '‚ö°' : ''}</h4>
            <span>{author}</span>
        </a>
        <div style={{flexGrow: 1}}/>
        {onRemove && <button className={'anti-button'} onClick={onRemove} title="Remove">‚ï≥</button>}
    </div>
);

export default function IndexView({ props }) {
    const [uploadPath, setUploadPath] = useState(null);
    const [author, setAuthor] = useState(localStorage.getItem('author') || '');
    const [uploadLoading, setUploadLoading] = useState(false);
    const [uploadComplete, setUploadComplete] = useState(false);
    const [uploadResponse, setUploadResponse] = useState(null);
    const [uploadError, setUploadError] = useState(null);
    const [recentUploads, setRecentUploadsState] = useState(getRecentUploads());
    const [notebooks, setNotebooks] = useState([]);
    const filepickerRef = useRef();

    useEffect(() => {
        fetch(`/api/notebooks`)
            .then(response => response.json())
            .then(body => setNotebooks(body))
            .catch(console.log);
    }, [recentUploads]);

    function handleUploadChange(e) {
        setUploadPath(e.target.value.split(/[/\\]/g).reverse()[0]);
    }
    function handleUpload() {
        const formData = new FormData();
        formData.append('notebook', filepickerRef.current.files[0]);
        formData.append('author', author.length === 0 ? 'Anonymous' : author);

        localStorage.setItem('author', author);
        
        setUploadLoading(true);

        fetch(`/api/notebooks`, {
            method: 'POST',
            body: formData
        })
            .then(response => response.json())
            .then(body => {
                setUploadResponse(body);
                recentUploads.unshift(body);
                setRecentUploads(recentUploads);
                setRecentUploadsState(getRecentUploads());

                setUploadLoading(false);
                setUploadComplete(true);
            })
            .catch((err) => {
                console.log(err);
                setUploadLoading(false);
                setUploadError(true);
            });
    }

    return (
        <div className="Index-root">
            <PlutoStyle>
                <section>
                    <h1 style={{marginTop: '1.5em'}}>Publish Pluto!</h1>
                    <p>
                        Share your Pluto notebooks like never before! Simply drag your notebook file somewhere on this page and we'll do the rest. Interactivity with <code>@bind</code> is supported out-of-the-box.
                    </p>
                    <p>
                        If your notebook requires certain external packages see this article for publishing notebooks with <a href="">package installation instructions included</a>. Even so certain packages may still fail to work properly, in which case you must also upload a <Link to="/faq#state-files">Pluto state file</Link> alongside your notebook file.
                    </p>
                </section>
                <section>
                    <h2>üìì Upload Your Notebook</h2>
                    <div style={{display: uploadPath ? 'none' : 'block'}}>
                        <input ref={filepickerRef} type="file" id="notebook-upload" hidden onChange={handleUploadChange}/>
                        <label htmlFor="notebook-upload" className="notebook-upload-label">Click or drag a file here</label>
                    </div>
                    { uploadPath && (
                        <div className="notebook-placeholder">
                            <div className="notebook-title">
                                <h3>{uploadPath}</h3>
                                <input type="text" value={author} readOnly={(uploadLoading || uploadComplete) ? true : false} placeholder="Add an author..." onChange={(e) => setAuthor(e.target.value)}/>
                            </div>
                            <div style={{flexGrow: 1}}/>
                            { !uploadLoading && !uploadComplete && !uploadError && <>
                                <button className="upload-button" onClick={handleUpload}>Upload</button>
                                <a onClick={() => {
                                    setUploadPath(null);
                                    filepickerRef.current.value = '';
                                }} title="Cancel">‚ï≥</a>
                            </>}
                            { uploadLoading && !uploadComplete && <>
                                <span style={{margin: 'auto 45px auto 0', textAlign: 'center'}}>This may take a while.<br></br><a href="/faq" target="_blank">Learn More</a></span>
                                <Spinner style={{marginRight: 15}}/>
                            </>}
                            { uploadComplete && <>
                                <span style={{margin: 'auto 25px auto 0', textAlign: 'center'}}>Your notebook is published!<br/><a href={`/nb/${uploadResponse.path}`}>See it live</a></span>
                                <button onClick={() => {
                                    filepickerRef.current.value = '';
                                    setUploadPath(null);
                                    setUploadComplete(false);
                                }}>Hooray! üéâ</button>
                            </>}
                            { uploadError && <>
                                <span style={{margin: 'auto 25px auto 0', textAlign: 'center'}}>There was an error. (‚î¨‚î¨Ôπè‚î¨‚î¨)</span>
                                <button onClick={() => {
                                    setUploadError(null);
                                }}>Go back</button>
                            </>}
                        </div>
                    )}

                    {/* { recentUploads.length > 0 && <>
                        <h3>Recent Uploads</h3>
                        { recentUploads.map((notebook, i) => (
                            <Notebook key={notebook._id} onRemove={() => {
                                recentUploads.splice(i, 1);
                                setRecentUploads(recentUploads);
                                setRecentUploadsState(getRecentUploads());
                            }} {...notebook}/>
                        ))}
                    </>} */}
                </section>
                <section>
                    <h2>üî• Notebooks</h2>
                    { notebooks.length > 0 && notebooks.map((notebook) => (
                        <Notebook key={notebook._id} {...notebook}/>
                    ))}
                </section>
            </PlutoStyle>
        </div>
    );
}


// Right now this is done through localStorage, although it will likely become user-based
// once user authentication becomes supported
function getRecentUploads() {
    try {
        return JSON.parse(localStorage.getItem('recentUploads') || '[]');
    }
    catch(e) {
        return [];
    }
}
function setRecentUploads(recentUploads) {
    localStorage.setItem('recentUploads', JSON.stringify(recentUploads));
}
